// 
import { motion } from 'framer-motion';
import Slider from 'react-slick';
// 
import HomeSlider1 from '../../img/homeSlider1.jpg';
import HomeSlider2 from '../../img/homeSlider2.jpg';
import HomeSlider3 from '../../img/homeSlider3.jpg';
import Park from '../../img/park.png';
import Serve from '../../img/serve.png';
import Cargo from '../../img/cargo.png';
import Info1 from '../../img/.jpg';
import Info2 from '../../img/1959.jpg';

function Home() {
  // react-slick Slider 
  const settings = {
    dots: true,
    fade: true,
    infinite: true,
    autoplay: true,
    arrows: false,
    speed: 2000,
    autoplaySpeed: 4000,
    cssEase: 'ease-out',
  };

  //  
  const serveVariants = {
    visible: {
      opacity: 1,
      scale: 1,
      transition: { duration: 2 },
    },
    hidden: { opacity: 0, scale: 0 },
  };

  // 
  const infoVariants = {
    leftVisible: {
      opacity: 1,
      x: 0,
      transition: { duration: 1.5 },
    },
    leftHidden: {
      opacity: 0,
      x: '-5%',
    },
    rightVisible: {
      opacity: 1,
      x: 0,
      transition: {duration: 1.5,},
    },
    rightHidden: {
      opacity: 0,
      x: '5%',
    }
  };

  return (
    <>
      <main className="front-main">
        {/*  section */}
        <section className="section-slider">
          <Slider {...settings}>
            <div className="section-slider__card">
              <img
                src={HomeSlider1}
                alt=""
                className="section-slider__card__img"
              />
            </div>
            <div className="section-slider__card">
              <img
                src={HomeSlider2}
                alt=""
                className="section-slider__card__img"
              />
            </div>
            <div className="section-slider__card">
              <img
                src={HomeSlider3}
                alt=""
                className="section-slider__card__img"
              />
            </div>
          </Slider>
        </section>

        {/*  */}
        <section className="section-serve">
          <h2 className="section-serve__title"></h2>
          <motion.div
            className="section-serve__wrapper"
            initial="hidden"
            animate="visible"
            variants={serveVariants}
          >
            <div className="section-serve__wrapper__data">
              <img
                src={Park}
                alt="park"
                className="section-serve__wrapper__data__img"
              />
              <h2 className="section-serve__wrapper__data__title"></h2>
              <p className="section-serve__wrapper__data__content">
                
                <br /> 9:00 ~ 17:00
              </p>
            </div>
            <div className="section-serve__wrapper__data">
              <img
                src={Serve}
                alt="serve"
                className="section-serve__wrapper__data__img"
              />
              <h3 className="section-serve__wrapper__data__title"></h3>
              <p className="section-serve__wrapper__data__content">
                
                <br /> 9:00 ~ 20:00
              </p>
            </div>
            <div className="section-serve__wrapper__data">
              <img
                src={Cargo}
                alt="cargo"
                className="section-serve__wrapper__data__img"
              />
              <h3 className="section-serve__wrapper__data__title"></h3>
              <p className="section-serve__wrapper__data__content">
                
                <br /> 9:00 ~ 20:00
              </p>
            </div>
          </motion.div>
        </section>

        <section className="section-info">
          <h2 className="section-info__title"></h2>
          <div className="section-info__wrapper">
            <motion.div
              className="section-info__wrapper__card"
              initial= "leftHidden"
              whileInView="leftVisible"
              variants={infoVariants}
            >
              <div className="section-info__wrapper__card__text">
                <h2 className="section-info__wrapper__card__text__title">
                  
                </h2>
                <p>
                   <br />
                   <br />
                  
                </p>
              </div>
              <img
                src={Info1}
                alt=""
                className="section-info__wrapper__card__img"
              />
            </motion.div>
            <motion.div
              className="section-info__wrapper__card"
              initial= "rightHidden"
              whileInView= "rightVisible"
              variants={infoVariants}
            >
              <img
                src={Info2}
                alt=""
                className="section-info__wrapper__card__img"
              />
              <div className="section-info__wrapper__card__text">
                <h2 className="section-info__wrapper__card__text__title">
                  
                </h2>
                <p>
                   <br />
                   <br />
                   <br />
                   <br />
                   <br />
                </p>
              </div>
            </motion.div>
          </div>
        </section>
      </main>
    </>
  );
}

export default Home;
